.app {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    background-color: #ddd;
    font-family: sans-serif;
    font-size: 16px;
}

.app * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.app__songs-controls {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    -ms-flex-direction: column;
        flex-direction: column;
}

.app__search {
    padding: 1em;
}

.app__songs-list {
    overflow: auto;
}

.app__song-details {
    overflow: hidden;
    -ms-flex: 0 1;
        flex: 0 1;
    border-left: 1px solid #bbb;
}

.app__mobile-player {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    height: 0;
    -webkit-transition: all linear 250ms;
    -o-transition: all linear 250ms;
    transition: all linear 250ms;
    border-top: 1px solid #bbb;
    background-color: #ddd;
}

.app.app--show-mobile-player .app__mobile-player {
    height: 70px;
    -webkit-transition: all linear 250ms;
    -o-transition: all linear 250ms;
    transition: all linear 250ms;
}

.app.app--has-details .app__song-details {
    padding: 1em;
    -webkit-transition: all linear 250ms;
    -o-transition: all linear 250ms;
    transition: all linear 250ms;
    -ms-flex-preferred-size: 70%;
        flex-basis: 70%;
}



